.App {
	text-align: center;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}
/* RoomPage Styling */
body {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: "Poppins", sans-serif;
	background: linear-gradient(135deg, #4e54c8, #8f94fb);
	color: #fff;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100vh;
}

.container {
	text-align: center;
	padding: 1.7rem;
	border-radius: 16px;
	background: rgba(0, 0, 0, 0.6);
	box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
	max-width: 900px;
	width: 100%;
}

h1 {
	font-size: 2.5rem;
}

h4 {
	font-size: 1.5rem;
}

button {
	padding: 12px 24px;
	font-size: 1rem;
	margin: 1rem;
	border: none;
	border-radius: 8px;
	cursor: pointer;
	transition: background 0.3s;
}

button:hover {
	background: #6a82fb;
}

.video-container {
	display: flex;
	justify-content: space-around;
	/* margin-top: 2rem; */
	flex-wrap: wrap;
}

.video-wrapper {
	border: 3px solid #fff;
	border-radius: 12px;
	overflow: hidden;
	margin: 1rem;
}

.stream-title {
	margin-top: 1rem;
	font-size: 1.2rem;
}

form {
	display: flex;
	flex-direction: column;
	align-items: center;
}

label {
	font-size: 1.2rem;
	margin: 0.5rem 0;
}

input {
	padding: 0.8rem;
	margin-bottom: 1rem;
	border: none;
	border-radius: 8px;
	width: 80%;
	max-width: 400px;
	font-size: 1rem;
	outline: none;
}

input:focus {
	box-shadow: 0 0 8px #45aaf2;
}

form button {
	padding: 12px 24px;
	font-size: 1rem;
	margin-top: 1rem;
	border: none;
	border-radius: 8px;
	cursor: pointer;
	background: linear-gradient(90deg, #ff5f6d, #ffc371);
	color: white;
	transition: background 0.3s, transform 0.2s;
}

form button:hover {
	background: linear-gradient(90deg, #32ff7e, #7efff5);
	transform: scale(1.05);
}
